/* Reset */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
a{
  text-decoration: none;
}
/* --- */
.loading{
  text-align: center;
}
.movie-wrap{
  padding: 100px;
}
.title{
  text-align: center;
  font-size : 46px;
  margin-bottom: 40px;
}

.movie-area a{
  text-decoration: none;
  color: #000;
}
.movie-area{
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  overflow: auto;
  gap: 20px;
}
.movie-box{
  padding: 20px;
}
.more{
  display: flex;
  align-items: center;
  font-size: 24px;
  font-weight: 700;
  margin-top: 20px;
}
.more-arr{
  margin-left: 20px;
  display: inline-block;
  width: 25px;
  height: 25px;
  background: url("https://cdn-icons-png.flaticon.com/512/271/271226.png?w=360") no-repeat;
  background-size: 100%;
  animation: arrMove 1s linear infinite alternate;;
}


.detail-box{
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
  padding: 60px;
}
.detail-title{
  font-size: 32px;
  margin: 16px 0;
}
.detail-rating,
.detail-year{
  font-size: 24px;
  margin-bottom: 16px;
}
.genres-area{
  display: flex;
  align-items: top;
  justify-content: center;
  margin-bottom: 16px;
}
.genres-title{
  font-size: 24px;
  margin-right: 10px;
}
.genres-list li{
  font-size: 20px;
  list-style: none;
  text-align: left;
}
.detail-summury{
  max-width: 800px;
  margin: 0 auto;
}
@keyframes arrMove{
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}